
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "AltStatsScale",

  props: {
    value: { type: Number as PropType<number>, required: true },
    title: String,
  },

  emits: ["update:value"],

  computed: {
    valueModel: {
      get() {
        return this.value;
      },
      set(value: number): void {
        this.$emit("update:value", value);
      },
    },
  },

  methods: {
    decrement(): void {
      if (this.valueModel > 0) {
        this.valueModel--;
      }
    },

    increment(): void {
      if (this.valueModel < 6) {
        this.valueModel++;
      }
    },

    getIcon(value: number) {
      switch (value) {
        case 1:
          return "mdi-dice-d4";
        case 2:
          return "mdi-dice-d6";
        case 3:
          return "mdi-dice-d8";
        case 4:
          return "mdi-dice-d10";
        case 5:
          return "mdi-dice-d12";
        case 6:
          return "mdi-dice-d20";
      }
      return "mdi-dice-d4";
    },
  },
});
